/*анимация заголовка*/
@keyframes anime-line {
    0%{width: 0px; margin-left: 0%}
    25%{width: 100%; margin-left: 0%;}
    75%{width: 0; margin-left: 100%; margin-right: 0%;}
    76%{width: 0; margin-left: 50%; margin-right: 50%;}
    100%{width: 30%; margin-left: 35%; margin-right: 35%;}
}

@keyframes anime-one-text {
    0%{}
    45%{opacity: 0; transform: scaleY(-1);}
    47.5%{opacity: 0;}
    47.6%{opacity: 1;}
    50%{opacity: 1; transform: scaleY(1);}
    65%{opacity: 1; transform: scaleY(1);}
    75%{opacity: 0; transform: translateY(0px); transform: scaleY(1) translateY(0px);}
    80%{opacity: 0; transform: scaleY(1) translateY(30px);}
    100%{opacity: 1; transform: translateY(30px);}
}

@keyframes anime-two-text {/*главный блок*/
    0%{}
    15%{opacity: 0;}
    25%{opacity: 1;}
    45%{opacity: 1; transform: scaleY(1);}
    47.5%{opacity: 1;}
    47.6%{opacity: 0;}
    50%{opacity: 0; transform: scaleY(-1) translate(0);;}
    75%{opacity: 0; transform: scaleY(1) translateY(0px);}
    80%{opacity: 0; transform: scaleY(1) translateY(-25px);}
    100%{opacity: 1; transform: scaleY(1) translateY(-25px);}
}

.line-anime-box{
    position: absolute;
    width: 70%; 
    height: 3px; 
    top: 0; 
    bottom: 0; 
    left: 15%; 
    right: 15%; 
    margin: auto;
}
    
.line-goanime{
    animation: anime-line 4s linear forwards;
    background-color: white; 
    height: 1px; 
    box-shadow: 1px 1px 2px rgb(184, 184, 184), 1px -1px 2px rgb(184, 184, 184); 
    opacity: .2;
}

.face-text .one-text-face, .face-text .two-text-face {
    position: absolute; 
    top: 0; 
    bottom: 0; 
    left: 0; 
    right: 0; 
    margin: auto; 
    opacity: 0;
}

.face-text .one-text-face{transform: scaleY(-1);}

.one-goanime{
    animation: anime-one-text 4s linear forwards; 
}

.two-goanime{
    animation: anime-two-text 4s linear forwards;
}

@keyframes anime-button-page{
    0%{bottom: 70px;}
    50%{bottom: 90px;}
    100%{bottom: 70px;}
}

.button-next-page{
    position: absolute;
    width: 50px; 
    height: 50px;
    bottom: 70px;
    margin: auto;
    left: 0; 
    right: 0;
