}

.slider{  /*сам слайдер полностью*/
    width: 75%; 
    max-width: 2000px;
    overflow: hidden;
    position: relative;
    margin: 0 auto;
    margin-top: 50px;
}

.slider-back{/*подложка градиент*/
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(white, rgb(165, 165, 165));
}

.slider-list {/*блок с вложенным списком*/
    width: 700px;
    height: 400px;
    margin: 100px auto;
    position: relative;
}

.slider-bottom{/*кнопка общие настройки*/
    width: 40px;
    height: 40px;
    position: absolute;
    opacity: .7;
    transition: .5s;
    top: 0px;
    bottom: 0px;
    margin: auto;
}

.slider-bottom:hover{/*кнопка эффект нажатия*/
    opacity: 1;
    transform: scale(1.2);
}

.slider-list .bottom-left{
    left: -50px;
    background-image: url(../image/left.png);
    background-size: cover;
}

.slider-list .bottom-right{
    right: -50px;
    background-image: url(../image/right.png);
    background-size: cover;
}

.slider ul{
    width: 100%;
    position: relative;
}

.slide{/*параметры каждого слайда*/
    width: 100%;
    height: 400px;
    position: absolute;
    opacity: 0;
    overflow: hidden;
    transition: .6s;
}

.slider .leftx{
    transform: translateX(-750px) scale(0.6);
    opacity: .8;
}

.slider .leftxx{
    transform: translateX(-1500px) scale(0.6);;
    opacity: 0;
}

.slider .rightx{
    transform: translateX(750px) scale(0.6);
    opacity: .8;
}

.slider .rightxx{
    transform: translateX(1500px) scale(0.6);;
    opacity: 0;
}

.slider .view{
    transform: translateX(0);
    opacity: 1;
}

.slide img{/*картинка в слайде*/
    width: 100%;
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin: auto;
    transition: .4s;
}

.slide img.increase{/*увеличение картинки при наведении курсора*/
    width: 120%;
    top: -10%;
    left:-10%;
}

.slide .slide-front{/*фон на картинку при наведении*/
    width: 100%;
    height: 100%;
    position: relative;
    background-color: black;
    opacity: 0;
    transition: .4s;
}

.slide .slide-front-view{/*проявление фона при наведении*/
    opacity: .7;
}

.slide .slide-click-icon{/*кружок с курсором при наведении на картинку*/
    background-image: url(../image/iconn.png);
    background-size: cover;
    width: 70px;
    height: 70px;
    position: absolute;
    opacity: 0;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin: auto;
    transform: rotate(0deg);
    transition: .4s;
}

.slide .slide-click-icon-view{/*анимация кружка с курсором*/
    transform: rotate(350deg);
    opacity: 1;
}
